.modal__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 31px 24px;
    gap: 20px;

    width: 600px;
    height: 121px;

    background: #FFFFFF;
    border-radius: 15px;

    z-index: 2;

    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 11px 15px -7px, rgba(0, 0, 0, 0.14) 0px 24px 38px 3px, rgba(0, 0, 0, 0.12) 0px 9px 46px 8px;
}

.modal__content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0;
    gap: 10px;
}

.status__icon {
    display: flex;
    width: 34px;
    height: 34px;
    flex-wrap: wrap;
    justify-content: center;
}

.status__image {
    width: 25.5px;
    object-fit: contain;
}

.modal__text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 5px;
}

.text__head {
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;

    color: #000000;
}

.text__info {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    color: #000000;
}

.modal__wrapper {
    position: fixed;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    inset: 0px;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-tap-highlight-color: transparent;
    z-index: 1;
}

@media(max-width: 800px) {
    .modal__container {
        width: -webkit-fill-available;
        height: fit-content;
        margin: 0 20px;
        top: 50%;
        left: 0;
        transform: translate(0%, -50%);
    }
}

@media(max-width: 560px) {
    .modal__container {
        padding: 15px 20px;
        border-radius: 10px;
    }

    .modal__content {
        gap: 5px;
    }

    .text__head {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
    }

    .text__info {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
    }

    .status__icon {
        width: 24px;
        height: 24px;
    }

    .status__image {
        width: 18px;
        margin-top: -4px;
    }
}